.about-page {
  height: 100%;
  width: 100%; }
  .about-page .image {
    position: absolute;
    height: 70%;
    width: 30%;
    min-width: 400px;
    margin: 0px 30px;
    float: left;
    background-image: url("../img/badge-photo.jpg");
    background-position: center bottom;
    background-repeat: no-repeat;
    background-size: auto 100%; }
  .about-page .content-page-content {
    position: relative;
    height: 100%;
    width: 60%;
    float: right;
    margin: 0px 20px; }



