html, body {
  background-color: #000;
  font-family: Arial, sans-serif;
  font-size: 18pt;
  color: #fff;
  text-align: justify; }

a {
  color: #aaa; }

.main .header {
  width: 100%;
  height: 50px; }
  .main .header .menu {
    width: 50px;
    height: 100%;
    background-image: url("../img/menu-icon.png");
    background-position: left middle;
    background-repeat: no-repeat;
    background-size: 50px;
    cursor: pointer;
    float: left; }
  .main .header .page-title {
    vertical-align: bottom;
    text-align: center;
    height: 100%;
    width: auto;
    color: #aaa;
    font-weight: bold;
    font-size: 18pt; }
  .main .header .menu-dropdown {
    width: 200px;
    font-family: Arial, sans-serif;
    position: fixed;
    top: 50px;
    padding: 5px 0 0 10px;
    z-index: 5; }
    .main .header .menu-dropdown .menu-dropdown-button {
      height: 50px;
      width: 80%;
      background-color: #000;
      padding: 20px 0 0 30px;
      cursor: pointer; }
      .main .header .menu-dropdown .menu-dropdown-button:hover {
        background-color: #888; }



